.main-recipes{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.cards-container {
	display: flex;
	padding-top: 30px;
    justify-content: center;
    align-items: center;
	width: 90%;
	height: 430px;
}

.new-recipe {
	display: inline-box;
    color: #fff;
	text-decoration: none;
	background: #bb5526;
    font-size: 35px;
	padding: 5px 200px;
	border-radius: 20px;
	border: none;
    margin-top: 100px;
    margin-bottom: 50px;
}

.new-recipe:hover {
	text-shadow: 0px 0px 4px rgba(255, 255, 255, 1);
	-webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
	-moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
	transition: all 0.4s ease 0s;
}