* {
    margin: 0;
    padding: 0;
    justify-content: center;
    align-items: center;
}

.app {
    width: 100vw;
    background: url("./resources/img/background.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    justify-content: center;
    align-items: center;
}

.navbar {
    display: inline-flex;
    align-items: center;
    min-width: 100%;
}

.navbar a {
    color: white;
    text-decoration: none;
}

.nav-title {
    color: rgb(255, 244, 236);
    text-shadow: 2px 2px 2px rgb(158, 76, 21);
    padding-top: 20px;
    width: 100%;
    height: 60px;
    text-align: center;
    font-family: 'Dancing Script', cursive;
	font-size: 60px;
}

.nav-bt {
    margin: 0 10px;
    width: 120px;
    padding: 5px;
    border: 1px solid #ffd6c3;
    color: seashell;
    font-size: 15px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #ca661452;
    text-align: center;
    }

.nav-bt:hover {
    background-color: #ca6614ce;
}
