.title-box {
    display: flex;
    width: 100vw;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title-text {
    color: #fff;
	background: #bb55269c;
    text-align: center;
    width: 70%;
    font-size: 35px;
    margin-bottom: 20px;
}

.home-bt {
    width: 120px;
    padding: 5px;
    border: 1px solid #ffd6c3;
    color: seashell;
    font-size: 15px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #ca661452;
    text-align: center;
    }

.home-bt:hover {
    background-color: #ca6614ce;
}
.field-box {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 70%;
}

.form-item{
    display: inline-flex;
    width: 100%;
}

.form-item label {
    font-size: 25px;
    color: rgb(255, 244, 236);
    text-shadow: 2px 2px 2px rgb(158, 76, 21);
    width: 20%;
    text-align: left;
    font-family: 'Dancing Script', cursive;
}

.form-item input{
    height: 25px;
    width: 80%;
    align-items: flex-end;
}

.update-bt {
    width: 120px;
    padding: 5px;
    margin-top: 20px;
    border: 1px solid #ffd6c3;
    color: rgb(255, 255, 255);
    font-size: 15px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #d46000;
    text-align: center;
    }

.update-bt:hover {
    background-color: #774821;
}