.title-box-rd {
    display: flex;
    width: 100vw;
    flex-direction: column;
    justify-content: center;
    padding-top: 80px;
}

.title-text-rd {
    color: #fff;
	background: #bb55269c;
    text-align: center;
    width: 70%;
    font-size: 35px;
    margin-bottom: 20px;
}

.recipe-detail-box {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
}

.recipe-rd {
    width: 220px;
    padding: 5px;
    margin: 20px;
    border: 1px solid #ffd6c3;
    color: rgb(255, 255, 255);
    font-size: 15px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #d46000;
    text-align: center;
    }

.recipe-rd:hover {
    background-color: #774821;
}