.home-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 90%;
    width: 90%;
    align-items: center;
    min-width: 80%;
    margin: 0 auto;
}

.form-box {
    display: flex;
    justify-content: center;
    width: 90%;
    margin-bottom: 25px;
}

.login-field {
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
    font-size: 20px;
    margin: 10px;
    min-width: 120px;
    float: left;
}

button.login-field:hover {
    color:#663f3f;
    background-color:#cf9f9f;
}

.signup-bt {
    color: #fff !important;
	text-decoration: none;
	background: #bb5526;
    font-size: 25px;
	padding: 5px 200px;
    margin-bottom: 80px;
	border-radius: 20px;
	display: inline-block;
	border: none;
	transition: all 0.4s ease 0s;
}

.signup-bt:hover {
	text-shadow: 0px 0px 4px rgba(255, 255, 255, 1);
	-webkit-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
	-moz-box-shadow: 0px 5px 40px -10px rgba(0,0,0,0.57);
	transition: all 0.4s ease 0s;
}