.title-box {
    display: flex;
    width: 100vw;
    height: 100vh;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title-text {
    color: #fff;
	background: #bb55269c;
    text-align: center;
    width: 70%;
    font-size: 35px;
    margin-bottom: 20px;
}

.home-bt {
    width: 120px;
    padding: 5px;
    border: 1px solid #ffd6c3;
    color: seashell;
    font-size: 15px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #ca661452;
    text-align: center;
    }

.home-bt:hover {
    background-color: #ca6614ce;
}
