.card-dt {
    display: flex;
    background-image: url("../../resources/img/card.jpg");
    background-size:contain;
    display: flex;
    width: 70%;
    height: 400px;
    margin: 10px;
    border: 2px solid rgb(168, 69, 11);
}

.img-container-dt {
    display: flex;
    justify-content: center;
    border: 1px solid salmon;
    overflow: hidden;
    width: 400px;
    height: 400px;
}

.photo-dt {
    object-fit: contain;
}

.card-title-dt {
    display: flex;
    width: 100%;
    justify-content: space-between;
    border: 1px solid salmon;
    font-size: 22px;
    text-shadow: 2px 2px 2px rgb(56, 25, 4);
    color: rgb(177, 80, 34);
    font-family: 'Dancing Script', cursive;
    text-align: center;
}

.card-text-container-dt {
    justify-content: center;
    display: inline-flex;
    flex-wrap: wrap;
}

.card-text-dt {
    font-size: 20px;
    text-shadow: 3px 3px 3px rgb(255, 255, 255);
    font-weight: 500;
    margin: 10px;
    text-align: center;
}

.buttons-recipe-card-dt {
    display: flex;
    margin: 10px;
    justify-content:last space-between;
}

.corner-left-dt {
    width: 60px;
    padding: 5px;
    margin-top: 20px;
    border: 1px solid #ffd6c3;
    color: rgb(255, 255, 255);
    font-size: 12px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #a152119f;
    text-align: center;
}

.corner-left-dt:hover {
    background-color: #45b818;
}

.corner-right-dt {
    width: 60px;
    padding: 5px;
    margin-top: 20px;
    border: 1px solid #ffd6c3;
    color: rgb(255, 255, 255);
    font-size: 12px;
    border-radius: 5px;
    text-decoration: none;
    font-family:'Roboto',sans-serif;
    font-weight: 600;
    background-color: #a152119f;
    text-align: center;
}

.corner-right-dt:hover {
    background-color: #e41919;
}
