.recipes-container {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    justify-content: space-around;
    align-items: center;
    height: 440px;
    overflow: scroll;
}

.recipes-container h2 {
    font-size: 70px;
    color: rgb(255, 244, 236);
    text-shadow: 2px 2px 2px rgb(158, 76, 21);
    text-align: center;
    font-family: 'Dancing Script', cursive;
    padding-bottom: 100px;
}